import React, { useState } from 'react';
import { SliderData } from './SliderData';
import { FaAngleRight, FaAngleLeft } from 'react-icons/fa';
import {Link} from 'react-scroll';

const ImageSlider = ({ slides }) => {
  const [current, setCurrent] = useState(0);
  const length = slides.length;

  const nextSlide = () => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const prevSlide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  if (!Array.isArray(slides) || slides.length <= 0) {
    return null;
  }

  return (
    <section className='slider'>
      <Link to="lastWorks" spy={true} smooth={true}><FaAngleLeft className='left-arrow' onClick={prevSlide} /></Link>
      <Link to="lastWorks" spy={true} smooth={true}><FaAngleRight className='right-arrow' onClick={nextSlide} /></Link>
      {SliderData.map((slide, index) => {
        return (
          <div
            className={index === current ? 'slide active' : 'slide'}
            key={index}
          >
            {index === current && (
              <img src={slide.image} alt='Превью работы' className='slider-image' />
            )}
          </div>
        );
      })}
    </section>
  );
};

export default ImageSlider;